@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css?family=Space+Mono&display=swap);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans:400,700);
html {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  background-color: #cddde2;
  height: 100%;
  overflow: hidden;
}

#root {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.App {
  height: 100%;
}

main {
  padding: 20px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  overflow: hidden;
  height: 100%;
}

.shadow {
  box-shadow: 10px 10px 13px -1px rgba(0, 0, 0, 0.09);
}

@media (max-width: 480px) {
  main {
    padding: 15px 0;
  }
}
header {
  z-index: 3;
  background-color: #5caac3;
  text-align: center;
  padding: 5px 10px;
  position: absolute;
  width: 100%;
}
header .brandIcon {
  display: inline;
  color: white;
  margin: auto;
  font-size: 2em;
}
header h1 {
  display: inline;
  margin: 0;
  padding: 0 15px;
  color: white;
  font-family: "Permanent Marker", cursive;
}

#settings {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  right: 3vw;
  height: 80%;
  width: auto;
}

#settings:hover {
  cursor: pointer;
}
#backgroundImage {
  z-index: -1;
  position: absolute;
  opacity: 0.05;
  background-repeat: repeat;
  height: 100%;
  width: 100%;
  background-image: url("https://www.wildtextures.com/wp-content/uploads/2011/09/checker-480x480.jpg");
}

.slideIconContainer {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  font-size: 8em;
  display: flex;
}
.slideIconContainer #slideIcon {
  margin: auto;
  flex: 1 1;
}

.frame {
  z-index: 1;
  display: flex;
  flex-direction: column;
  max-width: 40vh;
}

#frameContents {
  z-index: 1;
  background-color: white;
  border-radius: 4px;
  padding: 10px;
}

.petImage {
  flex: 1 1;
  padding: 10px;
}
.petImage img {
  border-radius: 3px;
  width: 100%;
}

.profile {
  padding: 10px;
  flex: 1 1;
}
.profile h2 {
  margin: 4px 0;
}
.profile .desc {
  margin-bottom: 10px;
}
.profile .desc p {
  display: inline;
  padding-right: 5px;
  font-style: italic;
  color: grey;
}
.controls {
  flex: 1 1;
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 20px;
}
.controls button {
  font-size: 2em;
  border-radius: 50%;
  border-style: none;
  padding: 0.5em;
  color: white;
}
.controls .add {
  background-color: #00b66a;
}
.controls .more {
  color: grey;
}
.controls .pass {
  background-color: #ff2f0d;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.controls button:hover {
  cursor: pointer;
}

@media (max-width: 480px) {
  .frame {
    height: 80%;
  }

  .controls button {
    font-size: 1.5em;
  }

  .profile {
    padding: 2px 10px;
  }
}
.modal {
  z-index: 2;
  position: absolute;
  display: flex;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  align-items: center;
}

.modalBody {
  margin: auto;
  background-color: white;
  border-radius: 4px;
  max-height: 75%;
  overflow: hidden;
  padding: 10px;
}
.modalBody #closeModal {
  font-size: 2em;
  color: black;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  text-align: right;
}
.modalBody #closeModal:hover {
  cursor: pointer;
}

.hide-modal {
  display: none;
}

#enlargedImage .modalBody {
  height: 75%;
  width: 75%;
  display: flex;
  flex-direction: column;
}
#enlargedImage .modalBody div {
  flex: 1 1;
  align-items: center;
  overflow: hidden;
  display: flex;
}
#enlargedImage .modalBody div img {
  margin: auto;
  height: 100%;
  justify-content: center;
}
footer {
  color: grey;
  text-align: center;
  font-family: "Space Mono", monospace;
  font-size: 12px;
  position: absolute;
  bottom: 0;
  width: 100%;
}
.cornerRibbon {
  /* The ribbons */
  /* Custom styles */
  /* Different positions */
  /* Colors */
}
.cornerRibbon .corner-ribbon {
  font-size: 16px;
  width: 260px;
  background: #e43;
  position: absolute;
  top: 20px;
  left: -45px;
  text-align: center;
  line-height: 50px;
  letter-spacing: 1px;
  color: #000000;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}
.cornerRibbon .corner-ribbon.sticky {
  position: fixed;
}
.cornerRibbon .corner-ribbon.shadow {
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
}
.cornerRibbon .corner-ribbon.top-left {
  top: 25px;
  left: -50px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}
.cornerRibbon .corner-ribbon.top-right {
  top: 25px;
  right: -50px;
  left: auto;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
.cornerRibbon .corner-ribbon.bottom-left {
  top: auto;
  bottom: 25px;
  left: -50px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
.cornerRibbon .corner-ribbon.bottom-right {
  top: auto;
  right: -60px;
  bottom: 45px;
  left: auto;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}
.cornerRibbon .corner-ribbon.white {
  background: #f0f0f0;
  color: #555;
}
.cornerRibbon .corner-ribbon.black {
  background: #333;
}
.cornerRibbon .corner-ribbon.grey {
  background: #999;
}
.cornerRibbon .corner-ribbon.blue {
  background: #39d;
}
.cornerRibbon .corner-ribbon.green {
  background: #2c7;
}
.cornerRibbon .corner-ribbon.turquoise {
  background: #1b9;
}
.cornerRibbon .corner-ribbon.purple {
  background: #95b;
}
.cornerRibbon .corner-ribbon.red {
  background: #e43;
}
.cornerRibbon .corner-ribbon.orange {
  background: #e82;
}
.cornerRibbon .corner-ribbon.yellow {
  background: #ec0;
}

@media (max-width: 480px) {
  .cornerRibbon {
    display: none;
  }
}
