.App {
  height: 100%;
}

main {
  padding: 20px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  overflow: hidden;
  height: 100%;
}

.shadow {
  -webkit-box-shadow: 10px 10px 13px -1px rgba(0, 0, 0, 0.09);
  -moz-box-shadow: 10px 10px 13px -1px rgba(0, 0, 0, 0.09);
  box-shadow: 10px 10px 13px -1px rgba(0, 0, 0, 0.09);
}

@media (max-width: 480px) {
  main {
    padding: 15px 0;
  }
}
