@import url("https://fonts.googleapis.com/css?family=Noto+Sans:400,700");

.cornerRibbon {
  /* The ribbons */
  .corner-ribbon {
    font-size: 16px;
    width: 260px;
    background: #e43;
    position: absolute;
    top: 20px;
    left: -45px;
    text-align: center;
    line-height: 50px;
    letter-spacing: 1px;
    color: #000000;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
  }

  /* Custom styles */

  .corner-ribbon.sticky {
    position: fixed;
  }

  .corner-ribbon.shadow {
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
  }

  /* Different positions */

  .corner-ribbon.top-left {
    top: 25px;
    left: -50px;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
  }

  .corner-ribbon.top-right {
    top: 25px;
    right: -50px;
    left: auto;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }

  .corner-ribbon.bottom-left {
    top: auto;
    bottom: 25px;
    left: -50px;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }

  .corner-ribbon.bottom-right {
    top: auto;
    right: -60px;
    bottom: 45px;
    left: auto;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
  }

  /* Colors */

  .corner-ribbon.white {
    background: #f0f0f0;
    color: #555;
  }
  .corner-ribbon.black {
    background: #333;
  }
  .corner-ribbon.grey {
    background: #999;
  }
  .corner-ribbon.blue {
    background: #39d;
  }
  .corner-ribbon.green {
    background: #2c7;
  }
  .corner-ribbon.turquoise {
    background: #1b9;
  }
  .corner-ribbon.purple {
    background: #95b;
  }
  .corner-ribbon.red {
    background: #e43;
  }
  .corner-ribbon.orange {
    background: #e82;
  }
  .corner-ribbon.yellow {
    background: #ec0;
  }
}

@media(max-width: 480px){
    .cornerRibbon{
        display: none;
    }
}
